@media (max-width: 1200px) {
    .promo {
        height: 600px;
        img {
            width: 900px; }
        &_btn {
            top: -74px; } }
    .sub_promo {
        &_item_text {
            padding: 0 10px; } }
    .easy {
        &_header {
            h2,&_round {
                top: 32%; } }
        &_block {
            .easy_item {
                height: 120px;
                &_1,&_2,&_3,&_4 {
                    align-items: center; } } } }
    .tasks {
        &_block {
            .tasks_item_text {
                padding: 0px 40px 0 26px;
                width: 235px; } } }
    .partners {
        &_header {
            h2 {
                top: 0; } }
        &_block {
            margin-top: 165px; } } }
@media (max-width: 991px) {
    .header {
        .nav {
            &_item {
                width: 100px; } } }
    .promo {
        &_img {
            width: 700px; }
        h1 {
            margin-top: 95px;
            font-size: 60px;
            line-height: 84px; }
        &_text {
            margin-top: 30px; } }
    .area {
        &_item_text {
            padding: 0 10px; } }
    .tasks {
        &_header {
            transform: translateY(-120px); }
        &_block {
            margin-top: 121px; } } }

@media (max-width: 767px) {
    .header {
        position: relative;

        .nav {
            position: fixed;
            flex-direction: column;
            align-content: center;
            justify-content: space-around;
            background-color: rgba(0, 0, 0, 0.5);
            width: 50%;
            left: 50%;
            height: 70vh;
            z-index: +100;
            &_item {
                border-radius: 10px;
                margin: 5px; } }
        .nav_active {
            right: 0; }
        .hamburger {
            position: absolute;
            background-color: #ffffff;
            border-radius: 5px;
            box-shadow: -3px -3px 9px 5px rgba(0,0,0, .2 );
            width: 32px;
            height: 32px;
            top: 2px;
            right: 2px;
            span {
                display: block;
                background-color: #000;
                width: 28px;
                height: 3px;
                margin: 6px auto; } }
        .hamburger_active span {
            &:nth-child(1) {
                transform: translateY(9px) rotate(-45deg); }
            &:nth-child(2) {
                display: none; }
            &:nth-child(3) {
                transform: translateY(0) rotate(45deg); } } } }










