@import "media";

@mixin box {
    margin: -20px 80px 0 80px;
    padding: 30px 0 30px 0;
    background-color: #ffffff;
    border-radius: 20px;
    box-shadow: -3px -3px 9px 5px rgba(0,0,0, .2 ); }
body {
    background-color: pink; }

h2 {
    color: #1b8c40;
    font-size: 50px;
    font-weight: 500;
    line-height: 43px; }
a {
    text-decoration: none; }
.up_click {
    position: fixed;
    right: 15px;
    bottom: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    background-color: #000000;
    opacity: 0.8;
    box-shadow: 0px 0px 7px 0px #ffffff;
    border-radius: 10px;
    z-index: 999;
    border: solid 2px #000000;
    &:hover {
        background-color: #fff;
        border: solid 2px #f69a24; }
    &_a {
        color: #fff;
        width: 100%;
        height: 100%;
        text-align: center;
        font-size: 24px;
        &:hover {
            color: #f69a24; } } }

.header {
    display: block;
    width: 100%;
    position: relative;
    background-color: #f69a24;
    z-index: 2;
    img {
        margin-top: 3px; } }
.nav {
    display: flex;
    justify-content: space-between;
    &_item {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 130px;
        height: 30px;
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
        background-color: #ffffff;
        box-shadow: 0 0 30px rgba(0,0 ,0, .4 );
        // box-shadow: 3px 3px 3px 2px rgba(0,0 ,0, .2 )
        transition: 0.2s;
        a {
            color: #000000; }
        &:hover {
            height: 35px; } } }
.promo {
    position: relative;
    min-height: 645px;
    margin-top: -1px;
    background-color: #f69a24;
    z-index: 1;
    padding-bottom: 95px;
    .bg {
        position: absolute;
        max-width: 1120px;
        margin-left: auto;
        margin-right: auto;
        left: 0;
        right: 0;
        text-align: center; }


    h1 {
        position: relative;
        font-family: Playfair display;
        font-size: 127px;
        font-weight: 700;
        margin-top: 90px;
        color: #1b8c40;
        line-height: 95px; }
    &_text {
        position: relative;
        margin-top: 35px;
        font-size: 30px;
        line-height: 32px; }
    &_btn {
        position: relative;
        margin-top: 155px;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 52px;
        width: 300px;
        font-size: 40px;
        font-weight: 500;
        border: none;
        border-radius: 50px;
        background-color: #ffffff;
        box-shadow: 0 0 30px rgba(0, 0, 0, .2);
        &:hover {
            background: #1b8c40; } } }
.sub_promo {
    @include box;
    position: relative;
    z-index: 2;
    &_item {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        text-align: center;
        img {
            padding: 0 60px 0 60px;
            height: 123px; }
        &_text {
            margin-top: 10px;
            font-size: 23px; } } }
.easy {
    position: relative;
    padding: 130px 0;
    &_header {
        &_round {
            position: absolute;
            width: 160px;
            height: 160px;
            background-color: #b2e35c;
            border-radius: 100%;
            transform: translate(58%, 42%); }
        h2 {
            position: absolute;
            transform: translate(79%, 34%); } }
    &_block {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: flex-end; }
    &_item {
        margin: 5px 5px;
        padding: 10px;
        width: 424px;
        height: 139px;
        font-size: 23px;
        line-height: 28px;
        &_1 {
            display: flex;
            padding-left: 130px;
            align-items: end;
            text-align: right;
            background-color: #f1ece6; }
        &_2 {
            display: flex;
            padding-right: 130px;
            align-items: end;
            text-align: left;
            background-color: #eafec6; }
        &_3 {
            display: flex;
            padding-left: 130px;
            align-items: start;
            text-align: right;
            background-color: #b2e35c; }
        &_4 {
            display: flex;
            padding-right: 130px;
            align-items: start;
            text-align: left;
            background-color: #f1ece6; } } }
.area {
    @include box;
    &_item {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        img {
            padding: 0 60px 0 60px;
            height: 123px; }
        &_text {
            margin-top: 10px;
            font-size: 23px;
            text-align: center; } } }
.tasks {
    position: relative;
    padding: 130px 0 70px 0;
    &_header {
        h2 {
            position: absolute;
            transform: translate(79%, 92%); } }
    &_block {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: flex-end; }
    &_item {
        &_round {
            position: absolute;
            height: 67px;
            width: 67px;
            border-radius: 100%;
            background-color: #b2e35c;
            z-index: -1; }
        &_text {
            margin: 5px 5px;
            padding: 0 106px 0 10px;
            width: 325px;
            height: 139px;
            font-size: 23px;
            line-height: 28px; } } }
.detailes {
    background-color: #f1ece6;
    padding: 50px 0;
    h2 {
        padding-bottom: 20px;
        text-align: center; }

    &_item {
        display: flex;
        justify-content: center;
        align-items: center;
        video {
            width: 400px;
            height: 300px; }
        &_order {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-end;
            margin-top: 40px;
            padding: 5px;
            width: 258px;
            height: 258px;
            background: url(../img/free.jpg) center (center / cover) no-repeat;
            &_line {
                margin-bottom: 5px;
                width: 90px;
                height: 5px;
                background-color: #ffffff; }
            &_main_text {
                padding-left: 72px;
                text-align: end;
                font-weight: 500;
                margin-bottom: 5px;
                font-size: 21px; }
            &_free_text {
                text-transform: uppercase;
                color: #fff;
                font-weight: 500;
                font-size: 21px; }
            &_email {
                margin-top: 30px;
                width: 100%;
                .resize {
                    width: 100%;
                    border-radius: 20px;
                    border-color: rgba(0,0,0,1);
                    background-color: rgba(0,0,0,0); }
                button {
                    margin-top: 5px;
                    width: 100%;
                    border-radius: 20px;
                    text-transform: uppercase;
                    font-weight: 500;
                    &:hover {
                        background-color: pink; } } } } } }

.partners {
    position: relative;
    padding: 80px 0;
    h2 {
        position: absolute;
        transform: translate(79%, 115%); }
    &_block {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center; }
    &_item {
        margin: 15px 30px; } }
.comment {
    min-height: 500px;
    padding: 100px 20px;
    background-color: #f69a24;
    .row {
        align-items: center; }
    &_wrapper {
        &_header {
            margin: 0;
            padding: 30px 0 30px 30px;
            background-color: #ffffff;
            border-radius: 20px;
            box-shadow: -3px -3px 9px 5px rgba(0,0,0, .2 ); }
        &_block {
            margin: 0;
            padding: 30px 0;
            background-color: #ffffff;
            border-radius: 20px;
            box-shadow: -3px -3px 9px 5px rgba(0,0,0, .2 );
            .photo {
                display: flex;
                justify-content: center;
                align-items: center;
                &_round {
                    margin: 0 10px;
                    &_icon {
                        border-radius: 100%;
                        width: 100px;
                        height: 100px;
                        background: url(../icons/photo_icon.png) center (center / cover) no-repeat; } } }
            .comment_text {
                margin: 20px;
                text-align: center;
                line-height: 18px; }
            .comment_company {
                margin: 20px;
                text-align: center;
                line-height: 18px;
                color: #1b8c40;
                font-weight: 500; } } } }
.footer {
    min-height: 450px;
    background-color: #000000;
    &_items {
        display: flex;
        flex-direction: column;
        h3 {
            margin: 30px 0;
            color: #b2e35c;
            text-align: start;
            font-size: 25px; }
        a {
            display: block;
            padding: 2px 2px 2px 0;
            color: #bababa;
            line-height: 15px;
            margin: 10px 0; } } }
















